import React, { Component } from 'react';
import {
  CCard,
  CCardBody,
  CCardFooter,
	CCol,
  CRow,
  CCardHeader
} from '@coreui/react'

import {
  CChartBar,
  CChartLine,
  CChartDoughnut,
  CChartRadar,
  CChartPie,
  CChartPolarArea
} from '@coreui/react-chartjs'

class Josss extends Component {
  constructor() {
    super()
    this.state = {
      data: []
    }
  }

componentDidMount(){
   const urlFetch = fetch('https://indonesia-covid-19.mathdro.id/api/harian')
   urlFetch.then( res => {
      if(res.status === 200)
         return res.json()   
   }).then( resJson => {
      this.setState({
          data: resJson.data
      })
   })
	 
}
render() {
    const { data } = this.state
		var label = data.map(function(e){
			return e.harike;
});
		var labels = data.map(function(e){
			return e.jumlahKasusKumulatif;
});
		var labelss = data.map(function(e){
			return e.jumlahPasienSembuh;
});
		var labelsss = data.map(function(e){
			return e.jumlahPasienMeninggal;
});
		var labelssss = data.map(function(e){
			return e.jumlahKasusDiperiksa;
});
console.log(labels)
    return (
      <div className="App">
			<div>
        <header className="App-header">
          <h1 className="App-title">Indonesia's Copit Graph</h1>
        </header>
			</div>
			<div>
    <>
      <CChartLine
            type="line"
						labels={label}
            datasets={[
              {
                label: 'Akumulasi Positif',
                backgroundColor: 'rgb(229, 83, 83)',
                data: labels
              },
							{
                label: 'Akumulasi Sembuh',
                backgroundColor: 'rgb(46, 184, 92)',
                data: labelss
              },
							{
                label: 'Akumulasi Meninggal',
                backgroundColor: 'rgb(249, 177, 21)',
                data: labelsss
              }
            ]}
            options={{
              tooltips: {
                enabled: true
              }
            }}           
          />
    </>
		</div>
      </div>
    );
  }
}

export default Josss;